import { directive as ClickOutside } from 'v-click-outside';
import excludeClickOutsideClasses from '@nextcloud/vue/dist/Mixins/excludeClickOutsideClasses';
import { getCurrentUser, getRequestToken } from '@nextcloud/auth';
import Avatar from '@nextcloud/vue/dist/Components/Avatar'; // import { loadState } from '@nextcloud/initial-state'
// const { userState } = loadState('user_status', 'profileEnabled', false)

export default {
  name: 'HeaderMenu',
  components: {
    Avatar: Avatar
  },
  directives: {
    ClickOutside: ClickOutside
  },
  mixins: [excludeClickOutsideClasses],
  props: {
    id: {
      type: String,
      required: true
    },
    ariaLabel: {
      type: String,
      default: ''
    },
    open: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      opened: this.open,
      clickOutsideConfig: {
        handler: this.closeMenu,
        middleware: this.clickOutsideMiddleware
      },
      user: getCurrentUser(),
      token: getRequestToken() // userState: userState,

    };
  },
  computed: {
    tokenUrl: function tokenUrl() {
      return encodeURIComponent(this.token);
    }
  },
  watch: {
    open: function open(newVal) {
      var _this = this;

      this.opened = newVal;
      this.$nextTick(function () {
        if (_this.opened) {
          _this.openMenu();
        } else {
          _this.closeMenu();
        }
      });
    }
  },
  mounted: function mounted() {
    document.addEventListener('keydown', this.onKeyDown);
  },
  beforeDestroy: function beforeDestroy() {
    document.removeEventListener('keydown', this.onKeyDown);
  },
  methods: {
    /**
     * Toggle the current menu open state
     */
    toggleMenu: function toggleMenu() {
      // Toggling current state
      if (!this.opened) {
        this.openMenu();
      } else {
        this.closeMenu();
      }
    },

    /**
     * Close the current menu
     */
    closeMenu: function closeMenu() {
      if (!this.opened) {
        return;
      }

      this.opened = false;
      this.$emit('close');
      this.$emit('update:open', false);
    },

    /**
     * Open the current menu
     */
    openMenu: function openMenu() {
      if (this.opened) {
        return;
      }

      this.opened = true;
      this.$emit('open');
      this.$emit('update:open', true);
    },
    onKeyDown: function onKeyDown(event) {
      // If opened and escape pressed, close
      if (event.key === 'Escape' && this.opened) {
        event.preventDefault();
        /** user cancelled the menu by pressing escape */

        this.$emit('cancel');
        /** we do NOT fire a close event to differentiate cancel and close */

        this.opened = false;
        this.$emit('update:open', false);
      }
    }
  }
};